@font-face {
  font-family: "TT Lakes Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TT Lakes Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 400; }

@font-face {
  font-family: "TT Lakes Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 500; }

@font-face {
  font-family: "TT Lakes Bold2";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 700; }

body {
  max-width: 1440px;
  height: auto;
  margin: auto;
  font-family: "TT Lakes Regular";
  background-color: #f8f8f8;
  color: black;
  font-size: 14px; }

.container {
  display: flex;
  position: relative;
  max-width: 1438;
  max-height: auto;
  background: #f8f8f8; }

.ta-s {
  text-align: left;
  width: 31%; }

.ta-c {
  text-align: center;
  width: 31%; }

.ta-e {
  text-align: right;
  width: 28%; }

.btlr-10 {
  border-top-left-radius: 10px; }

.btrr-10 {
  border-top-right-radius: 10px; }

.bblr-10 {
  border-bottom-left-radius: 10px; }

.bbrr-10 {
  border-bottom-right-radius: 10px; }

.mr-2 {
  margin-right: 2% !important; }

.ml-a {
  margin-left: auto !important; }

.page__section--img {
  max-width: 100%;
  max-height: 100%; }

@media (max-width: 1366px) {
  .container {
    display: flex;
    flex-direction: column; }
  .footer__style {
    font-size: 14px;
    display: flex;
    border-top: 1px solid #d9fff5; }
  .ta-s {
    text-align: left;
    width: 31%;
    margin: 0; }
  .ta-c {
    text-align: center;
    width: 31%;
    margin: 0; }
  .ta-e {
    text-align: right;
    width: 28%; } }

@media (max-width: 768px) {
  .footer__style {
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    width: 100%;
    text-align: center; }
  .ta-s {
    text-align: center;
    width: auto; }
  .ta-c {
    text-align: center;
    width: auto; }
  .ta-e {
    text-align: center;
    width: auto; } }

@media (max-width: 1366px) {
  .mail,
  .lang,
  .tel,
  .menu__nav,
  .menu_footer,
  .footer,
  .icon-search,
  .main__sidebar__menu,
  .page__header {
    display: none; }
  .sm {
    display: flex; }
  .line__gray {
    display: flex;
    height: 40px;
    align-items: center;
    margin: 0 2%; }
  .side__menu {
    max-width: 1366px;
    max-height: 100%;
    padding: 0%;
    margin-right: 0%;
    width: 100%; }
  .menu,
  .contacts__icon,
  .tools {
    display: flex;
    gap: 7%;
    margin-right: 15px;
    vertical-align: middle; }
  .sidebar__menu {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    max-width: 96%;
    width: 100%;
    background-color: #ffffff;
    padding: 2% 2%; }
  .burger {
    display: initial; }
  .main__menu {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  .closebtn__show {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(253, 251, 251, 0.897);
    z-index: 1; }
  .contacts {
    margin: 0; }
  .sm-show {
    font-family: "TT Lakes Regular";
    content: "";
    background-color: #ffffff;
    position: absolute;
    top: 0;
    left: 0;
    width: 384px;
    height: 100%;
    box-shadow: 0 0 10px #b4b1b1;
    z-index: 2; }
  .mm-show {
    padding: 3% 0 0 0; }
  .sidebar-wrapper-show {
    content: "";
    position: absolute;
    overflow-y: scroll;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(253, 251, 251, 0.897);
    z-index: 1; }
  .mn-show {
    list-style: none;
    padding: 6%; }
  .li__active-show {
    position: relative;
    padding-left: 6%;
    right: 7%;
    border-left: 3px solid #41f6d7;
    border-radius: 2px;
    color: #7e7e82; }
  .page__style {
    z-index: 1;
    background-color: rgba(253, 251, 251, 0.897); }
  .icon-show {
    display: flex;
    justify-content: space-evenly;
    padding: 5%; }
  .contacts-show {
    display: flex;
    flex-direction: column;
    padding: 5%; }
  .contacts__icon-show {
    display: none;
    margin-left: auto; }
  .tel-show {
    font-family: "TT Lakes Bold2"; }
  .lang-show {
    display: none;
    padding-left: 5%;
    gap: 5%; }
  .sm-closebtn {
    display: none; } }

@media (max-width: 1366px) and (max-width: 768px) {
  .sm-show {
    content: "";
    background-color: #ffffff;
    position: absolute;
    left: 0;
    width: 520px;
    height: 100%;
    box-shadow: 0 0 10px #b4b1b1;
    z-index: 2; }
  .contacts__icon-show {
    display: none;
    margin-left: 0;
    gap: 5%; }
  .contacts__icon {
    display: none;
    gap: 7px; }
  .tools {
    margin-left: 17px; }
  .main__menu {
    margin-left: 10px; }
  .side__menu {
    max-width: 768px;
    width: 100%;
    max-height: 100%;
    padding: 0%;
    margin-right: 0%; } }
