@font-face {
  font-family: "TT Lakes Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TT Lakes Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 400; }

@font-face {
  font-family: "TT Lakes Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 500; }

@font-face {
  font-family: "TT Lakes Bold2";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 700; }

body {
  max-width: 1440px;
  height: auto;
  margin: auto;
  font-family: "TT Lakes Regular";
  background-color: #f8f8f8;
  color: black;
  font-size: 14px; }

.container {
  display: flex;
  position: relative;
  max-width: 1438;
  max-height: auto;
  background: #f8f8f8; }

.ta-s {
  text-align: left;
  width: 31%; }

.ta-c {
  text-align: center;
  width: 31%; }

.ta-e {
  text-align: right;
  width: 28%; }

.btlr-10 {
  border-top-left-radius: 10px; }

.btrr-10 {
  border-top-right-radius: 10px; }

.bblr-10 {
  border-bottom-left-radius: 10px; }

.bbrr-10 {
  border-bottom-right-radius: 10px; }

.mr-2 {
  margin-right: 2% !important; }

.ml-a {
  margin-left: auto !important; }

.page__section--img {
  max-width: 100%;
  max-height: 100%; }

@media (max-width: 1366px) {
  .container {
    display: flex;
    flex-direction: column; }
  .footer__style {
    font-size: 14px;
    display: flex;
    border-top: 1px solid #d9fff5; }
  .ta-s {
    text-align: left;
    width: 31%;
    margin: 0; }
  .ta-c {
    text-align: center;
    width: 31%;
    margin: 0; }
  .ta-e {
    text-align: right;
    width: 28%; } }

@media (max-width: 768px) {
  .footer__style {
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    width: 100%;
    text-align: center; }
  .ta-s {
    text-align: center;
    width: auto; }
  .ta-c {
    text-align: center;
    width: auto; }
  .ta-e {
    text-align: center;
    width: auto; } }

.menu,
.main__sidebar__menu {
  display: flex;
  flex-direction: column;
  max-width: 306px;
  width: 100%;
  background-color: #ffffff; }

.side__menu {
  color: black;
  padding: 9% 10% 20% 10%; }

.icon {
  display: flex;
  justify-content: space-between; }

.search-icon {
  margin-right: 37%; }

.menu__nav {
  list-style: none;
  padding: 0%;
  font-family: "TT Lakes Medium"; }

a {
  text-decoration: none;
  color: black; }

.contacts {
  margin: 70% 0 0 0; }

li {
  margin: 10% 0 0 0; }

li:hover {
  position: relative;
  padding-left: 10%;
  right: 12%;
  border-left: 3px solid #41f6d7;
  border-radius: 2px;
  color: #7e7e82; }

.h1__active {
  position: relative;
  padding-left: 2%;
  right: 2%;
  border-left: 3px solid #41f6d7;
  border-radius: 2px; }

.ru_active {
  color: #7e7e82; }

.contacts__icon {
  display: flex;
  gap: 7%; }

.mail {
  font-family: "TT Lakes Medium"; }

.tel {
  font-family: "TT Lakes Bold2";
  font-size: 24px; }

.lang {
  display: flex;
  gap: 1%;
  font-family: "TT Lakes Medium"; }

.line__gray,
.burger,
.line__gray,
.sm-closebtn,
.i1-show,
.contacts__icon-show,
.tools {
  display: none; }
